<template>
    <div class="player" v-show="playList.length > 0">
        <transition name="normal"
                    @enter="enter"
                    @after-enter="afterEnter"
                    @leave="leave"
                    @after-leave="afterLeave"
        >
            <div class="normal-player" v-show="fullScreen">
            <div class="background">
                <img :src="currentSong.image" width="100%" height="100%" alt="">
            </div>
            <div class="top">
                <div class="back" @click="back">
                    <i class="icon-back"></i>
                </div>
                <h1 class="title" v-html="currentSong.name"></h1>
                <h2 class="subtitle" v-html="currentSong.singer"></h2>
            </div>
            <div class="middle"
            	@touchstart.prevent = "middleTouchStart"
            	@touchmove.prevent = "middleTouchMove"
            	@touchend = "middleTouchEnd"
            	>
                <div class="middle-l" ref = "middleL">
                    <div class="cd-wrapper" ref="cdWrapper">
                        <div class="cd" :class="cdCls">
                            <img :src="currentSong.image" class="image" alt="">
                        </div>
                    </div>
                    <div class="playing-lyric-wrapper">
                    	<div class="playing-lyric">{{ playingLyric }}</div>
                    </div>
                </div>
                <scroll class="middle-r" ref="lyricList" :data="currentLyric && currentLyric.lines">
                	<div class="lyric-wrapper">
                		<div v-if="currentLyric">
                			<p ref="lyricLine" class="text" 
                				:class="{'current':currentLineNum === index}"
                				v-for="(line,index) in currentLyric.lines"
                			>
                				{{ line.txt }}
                			</p>
                		</div>
                	</div>
                </scroll>
            </div>
            <div class="bottom">
            	<div class="dot-wrapper">
            		<span class="dot" :class="{'active':currentShow==='cd'}"></span>
            		<span class="dot" :class="{'active':currentShow==='lyric'}"></span>
            	</div>
                <div class="progress-wrapper">
                    <span class="time time-l"> {{ format(currentTime) }}</span>
                    <div class="progress-bar-wrapper">
                        <progress-bar :percent = 'percent' @percentChange = 'percentChange'></progress-bar>
                    </div>
                    <span class="time time-r">{{ format(currentSong.duration) }}</span>
                </div>
                <div class="operators">
                    <div class="icon i-left" @click="changeMode">
                        <i :class="iconMode" ></i>
                    </div>
                    <div class="icon i-left" :class="disableCls">
                        <i @click="prev" class="icon-prev"></i>
                    </div>
                    <div class="icon i-center">
                        <i :class="playIcon" @click="togglePlaying"></i>
                    </div>
                    <div class="icon i-right" :class="disableCls">
                        <i @click="next" class="icon-next"></i>
                    </div>
                    <div class="icon i-right">
                        <i class="icon" @click="toggleFavorite(currentSong)" :class="getFavoriteIcon(currentSong)"></i>
                    </div>
                </div>
            </div>
        </div>
        </transition>
        <transition class="mini">
            <div class="mini-player" v-show="!fullScreen" @click="open">
            <div class="icon" :class="cdCls">
                <img  width="40" height="40" :src="currentSong.image" alt="">
            </div>
            <div class="text">
                <h2 class="name" v-html="currentSong.name"></h2>
                <p class="desc" v-html="currentSong.singer"></p>
            </div>
            <div class="control">
                <progress-circle :radius="radius" :percent="percent">
                	<i @click.stop="togglePlaying" class="icon-mini" :class="miniIcon"></i>
                </progress-circle>
            </div>
            <div class="control" @click.stop="showPlaylist">
                <i class="icon-playlist"></i>
            </div>
           	
        </div>
        </transition>
        <play-list ref="playlist"></play-list>
        <audio ref="audio" @timeupdate="updateTime" :src="currentSong.url"
        	 @play="ready" @error="error"
        	 @ended="end"></audio>
    </div>
</template>

<script type="text/ecmascript-6">
	
   import {mapGetters,mapMutations,mapActions} from 'vuex'
   import animations from 'create-keyframe-animation'
   import {prefixStyle} from 'common/js/dom'
   import ProgressBar from 'base/progress-bar/progress-bar'
   import ProgressCircle from 'base/progress-circle/progress-circle'
   import {playMode} from 'common/js/config'
   import {shuffle} from 'common/js/util'
   import Lyric from 'lyric-parser'
   import Scroll from 'base/scroll/scroll'
   import PlayList from 'components/playlist/playlist'
   import {playerMixin} from 'common/js/mixin'
   
   const transform  = prefixStyle('transform')
   const transitionDuration = prefixStyle('transitionDuration')
    export default {
    	mixins:[playerMixin],
       data(){
           return {
               songReady:false,
               currentTime : 0,
               radius:32,
               currentLyric:null,
               currentShow:'cd',
               playingLyric:'',
               currentLineNum:0
           }
      	},
      	
      
       	created(){
       		this.touch={}
       	},
       	computed:{
           playIcon() {
               return this.playing ? 'icon-pause' : 'icon-play'
           },
           miniIcon(){
             return this.playing ? 'icon-pause-mini' : 'icon-play-mini'
           },
           cdCls() {
               return this.playing ? 'play' :'play pause'
           },
           iconMode(){
           	return this.mode === playMode.sequence?'icon-sequence':this.mode===playMode.loop ?'icon-loop':'icon-random'
           },
           disableCls () {  //给前进后退按钮加上类名
               return this.songReady ? "" : 'disable'
           },
		
           percent() {
               return this.currentTime/this.currentSong.duration
           },
           
           ...mapGetters([
               'fullScreen',
               'playList',
               'singer',
               'currentSong',
               'playing',
               'currentIndex',
               'mode',
               'sequenceList'
           ])
       },
        methods:{
        	showPlaylist() {  //展示歌曲列表
        		this.$refs.playlist.show()
        	},
           	updateTime(e){
               this.currentTime = e.target.currentTime
           	},
            ready(){  //音频可播放的时候派发的事件，生成一个标志位
                 this.songReady = true
                 this.savePlayHistory(this.currentSong)
            },
            error(){ //以防出现音频出错的情况
               this.songReady = true
            },
            end(){ //播放结束的时候触发。
            	if(this.mode === playMode.loop){
            		this.loop()
            	}else{
            		this.next()
            	}
            },
            loop(){
            	this.$refs.audio.currentTime=0
            	this.$refs.audio.play()
            	if(this.currentLyric) {
            		this.currentLyric.seek(0)
            	}
            },
            //改变播放模式
            changeMode(){
            	const mode = (this.mode + 1) % 3
            	this.SET_MODE(mode)
            	let list = null
            	if(mode === playMode.random){
            		list = shuffle(this.sequenceList)
            	}else{
            		list = this.sequenceList
            	}
            	this.resetCurrentIndex(list) //得到当前歌曲在播放列表总的索引  
            	//只是改变模式 找到索引是为了不让他跳转
            	this.SET_PLAYLIST(list)  //设置播放列表
            },
            resetCurrentIndex(list){ //找到当前播放的index的值 并且设置currentIndex
            	let index = list.findIndex( (item)=>{
            		return item.id === this.currentSong.id
            	})
            	this.SET_CURRENTINDEX(index)  //设置index
            },
            
           	prev(){
               	if(!this.songReady){
                   return;
               	}
               	if(this.playList.length === 1){
               		this.loop()
               	}else{
	               	let index = this.currentIndex - 1;
	               	if(index === -1 ){
	               	    index = this.playList.length - 1
	               	}
	              	this.SET_CURRENTINDEX(index)
	               	if(!this.playing){
	                   this.togglePlaying( )
	               	}
               	}
               	this.songReady = false
               
           	},
            next(){
            	
               	if(!this.songReady){
                   return;
               	}
               	if(this.playList.length === 1){
               		this.loop()
               	}else{
               		let index = this.currentIndex + 1
	                if(index === this.playList.length){
	                   index = 0
	                }
	                this.SET_CURRENTINDEX(index)  //修改index 触发mutation
	                if(!this.playing){
	                    this.togglePlaying( )  //触发playing更新为play状态  改变暂停按钮样式。
	                }
               	}
               	this.songReady = false
              
            },

           	back(){
               this.SET_FULLSCREEN(false)
           },
            open(){
               this.SET_FULLSCREEN(true)
            },
            enter(el,done){
                const {x,y,scale} = this._getPosAndScale()
                let animation = {
                    0 : {
                        transform:`translate3d(${x}px,${y}px,0) scale(${scale})`
                    },
                    60:{
                        transform:`translate3d(0,0,0) scale(1.1)`
                    },
                    100: {
                        transform:`translate3d(0,0,0) scale(1)`
                    }
                }
                animations.registerAnimation({
                    name:'move',
                    animation,
                    presets:{
                        duration:400,
                        easing:'linear'
                    }
                })
                animations.runAnimation(this.$refs.cdWrapper,'move',done)
            },
            afterEnter(){
                animations.unregisterAnimation('move')
                this.$refs.cdWrapper.animation=""
            },
            leave(el,done){
                this.$refs.cdWrapper.style.transition = 'all 0.4s'
                const {x,y,scale} = this._getPosAndScale();
                this.$refs.cdWrapper.style[transform] = `translate3d(${x}px,${y}px,0) scale(${scale})`
                this.$refs.cdWrapper.addEventListener('transitionend',done)
            },
            afterLeave(){
                this.$refs.cdWrapper.style.transition = ''
                this.$refs.cdWrapper.style[transform]= ''
            },

            _getPosAndScale(){
                const targetWidth = 40
                const paddingLeft = 40
                const paddingBottom = 30
                const paddingTop = 80
                const width = window.innerWidth * 0.8
                const scale = targetWidth/width
                const x = -(window.innerWidth/2 - paddingLeft)
                const y = window.innerHeight - paddingTop - width/2 - paddingBottom
                return {
                     x,
                     y,
                    scale
                }
            },
            togglePlaying(){
            	if(!this.songReady){ //只有音频准备好了才可以调用播放。
            		return 
            	}
                 this.SET_PLAYING(!this.playing)
            },
            
            getLyric(){
            	this.currentSong.getLyric().then( (lyric) => {
            		//将歌词lyric通过Lyric类 转化为currentLyric对象
            		if(this.currentSong.lyric!==lyric){
            			return
            		}
            		this.currentLyric = new Lyric(lyric,this.handleLyric)
//          		console.log(1)
//          		console.log(this.currentLyric)
            		if(this.playing){
            			this.currentLyric.play()
            		}
            	}).catch( ()=> {
            		console.log('catch')
            		this.currentLyric = null
            		this.playingLyric = ''
            		this.currentLineNum  = 0
            	})
            },
            handleLyric({lineNum,txt}){
            	this.currentLineNum = lineNum
            	if(lineNum > 5){
            		let lineEl = this.$refs.lyricLine[lineNum-5]  //取到大于五行的数据
            		this.$refs.lyricList.scrollToElement(lineEl,1000)
            		
            	}else{
            		this.$refs.lyricList.scrollTo(0,0,1000)
            	}
            	this.playingLyric = txt    //txt即为当前播放的歌词 赋值给playingLyric可以通过这个来进行。
            },
           middleTouchStart(e){
           		this.touch.initiated = true
           		//用来判断是否是一次移动
           		this.touch.moved  = false
           		const finger = e.touches[0]
           		this.touch.startX = finger.pageX
           		this.touch.startY = finger.pageY
           },
           middleTouchMove(e){
           		if(!this.touch.initiated){
           			return
           		}
           		const finger  = e.touches[0]
           		const deltaX = finger.pageX - this.touch.startX
           		const deltaY = finger.pageY - this.touch.startY
           		if(Math.abs(deltaX) < Math.abs(deltaY)) {
           			return; //用来判断是不是用户在翻动歌词
           		}
           		if(!this.touch.moved){
           			this.touch.moved=true
           		}
           		const left = this.currentShow === 'cd' ? 0 : -window.innerWidth
           		const offsetWidth = Math.min(0,Math.max(-window.innerWidth,left + deltaX ))
           		this.touch.percent = Math.abs( offsetWidth/window.innerWidth )
           		this.$refs.lyricList.$el.style[transform] = `translate3d(${offsetWidth}px,0,0)`
           		this.$refs.lyricList.$el.style[transitionDuration]=0
           		this.$refs.middleL.style.opacity = 1 - this.touch.percent
           		this.$refs.middleL.style[transitionDuration] = 0
           		
           	},
           middleTouchEnd(){
           		if(!this.touch.moved){
           			return
           		}
           		let offsetWidth
           		let opacity
           		if(this.currentShow === "cd") {
           			if(this.touch.percent > 0.1){
           				offsetWidth = -window.innerWidth
           				opacity = 0
           				this.currentShow = 'lyric'
           			}else{
           				offsetWidth = 0
           				opacity = 1
           			}
           		}else {
           			if(this.touch.percent < 0.9) {
           				offsetWidth = 0
           				this.currentShow = 'cd'
           				opacity = 1
           			}else{
           				offsetWidth = -window.innerWidth
           				opacity = 0
           			}
           		}
           		const time  = 300
           		this.$refs.lyricList.$el.style[transform] = `translate3d(${offsetWidth}px,0,0)`
           		this.$refs.lyricList.$el.style[transitionDuration] = `${time}ms`
           		this.$refs.middleL.style.opacity = opacity
           		this.$refs.middleL.style[transitionDuration] = `${time}ms`
           		this.touch.initiated = false
           },
            //格式化事件
            format(interval){
                interval = interval | 0
                const minute = this._pad(interval/60 | 0)
                const second =this._pad(interval % 60)

                return `${minute}:${second}`
            },
            //补0
            _pad(num,n = 2 ){
                let len = num.toString().length;
                 while(len < n){
                      num = '0' + num
                      len ++
                 }
                return num
            },
            
            percentChange(per){
    			const currentTime = this.currentSong.duration * per
    		
            	this.$refs.audio.currentTime = currentTime
            	if(!this.playing){
            		this.togglePlaying()
            	}
            	if(this.currentLyric) {
            		this.currentLyric.seek(currentTime*1000)  //当人为改变的时候寻址到指定播放时间地址。
            	}
            },
            ...mapMutations({
                SET_FULLSCREEN:'SET_FULLSCREEN',
                SET_PLAYING:'SET_PLAYING',
                SET_CURRENTINDEX : 'SET_CURRENTINDEX',
                SET_MODE:'SET_MODE',
                SET_PLAYLIST:'SET_PLAYLIST'
            }),
            ...mapActions([
            	'savePlayHistory'
            ])
        },
        watch:{
           currentSong(newSong,oldSong){
           		if(newSong.id===oldSong.id || !this.sequenceList.length){
           			return
           		}
           		
           		if(this.currentLyric){
           			this.currentLyric.stop()
           			this.currentTime = 0
           			this.playingLyric = ""
           			this.currentLineNum = 0
           		}
           		
           		clearTimeout(this.timer)   //能够播放的时候
           		
           	    this.timer = setTimeout( () => {
                   this.$refs.audio.play()
                   this.getLyric()   //在有数据传进来的时候就触发这个方法请求到歌词 然后通过currentLyric来接受
               },1000)
           },
            playing(newPlaying){
               	const audio = this.$refs.audio
                this.$nextTick( () => {
                    newPlaying ? audio.play() : audio.pause()
                })

            }

        },
        components:{
           ProgressBar,
           ProgressCircle,
           Scroll,
           PlayList
        }

    }

</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"
    .player
        .normal-player
            position: fixed
            left: 0
            right: 0
            top: 0
            bottom: 0
            z-index: 150
            background: $color-background
            .background
                position: absolute
                left: 0
                top: 0
                width: 100%
                height: 100%
                z-index: -1
                opacity: 0.6
                filter: blur(20px)
            .top
                position: relative
                margin-bottom: 25px
                .back
                    position absolute
                    top: 0
                    left: 6px
                    z-index: 50
                    .icon-back
                        display: block
                        padding: 9px
                        font-size: $font-size-large-x
                        color: $color-theme
                        transform: rotate(-90deg)
                .title
                    width: 70%
                    margin: 0 auto
                    line-height: 40px
                    text-align: center
                    no-wrap()
                    font-size: $font-size-large
                    color: $color-text
                .subtitle
                    line-height: 20px
                    text-align: center
                    font-size: $font-size-medium
                    color: $color-text
            .middle
                position: fixed
                width: 100%
                top: 80px
                bottom: 170px
                white-space: nowrap
                font-size: 0
                .middle-l
                    display: inline-block
                    vertical-align: top
                    position: relative
                    width: 100%
                    height: 0
                    padding-top: 80%
                    .cd-wrapper
                        position: absolute
                        left: 10%
                        top: 0
                        width: 80%
                        height: 100%
                        .cd
                            width: 100%
                            height: 100%
                            box-sizing: border-box
                            border: 10px solid rgba(255, 255, 255, 0.1)
                            border-radius: 50%
                            &.play
                                animation: rotate 20s linear infinite
                            &.pause
                                animation-play-state: paused
                            .image
                                position: absolute
                                left: 0
                                top: 0
                                width: 100%
                                height: 100%
                                border-radius: 50%
                    .playing-lyric-wrapper
                        width: 80%
                        margin: 30px auto 0 auto
                        overflow: hidden
                        text-align: center
                        .playing-lyric
                            height: 20px
                            line-height: 20px
                            font-size: $font-size-medium
                            color: $color-text-l
                .middle-r
                    display: inline-block
                    vertical-align: top
                    width: 100%
                    height: 100%
                    overflow: hidden
                    .lyric-wrapper
                        width: 80%
                        margin: 0 auto
                        overflow: hidden
                        text-align: center
                        .text
                            line-height: 32px
                            color: $color-text-l
                            font-size: $font-size-medium
                            &.current
                                color: $color-text
            .bottom
                position: absolute
                bottom: 50px
                width: 100%
                .dot-wrapper
                    text-align: center
                    font-size: 0
                    .dot
                        display: inline-block
                        vertical-align: middle
                        margin: 0 4px
                        width: 8px
                        height: 8px
                        border-radius: 50%
                        background: $color-text-l
                        &.active
                            width: 20px
                            border-radius: 5px
                            background: $color-text-ll
                .progress-wrapper
                    display: flex
                    align-items: center
                    width: 80%
                    margin: 0px auto
                    padding: 10px 0
                    .time
                        color: $color-text
                        font-size: $font-size-small
                        flex: 0 0 30px
                        line-height: 30px
                        width: 30px
                        &.time-l
                            text-align: left
                        &.time-r
                            text-align: right
                    .progress-bar-wrapper
                        flex: 1
                .operators
                    display: flex
                    align-items: center
                    .icon
                        flex: 1
                        color: $color-theme
                        &.disable
                            color: $color-theme-d
                        i
                            font-size: 30px
                    .i-left
                        text-align: right
                    .i-center
                        padding: 0 20px
                        text-align: center
                        i
                            font-size: 40px
                    .i-right
                        text-align: left
                    .icon-favorite
                        color: $color-sub-theme
            &.normal-enter-active, &.normal-leave-active
                transition: all 0.4s
                .top, .bottom
                    transition: all 0.4s cubic-bezier(0.86, 0.18, 0.82, 1.32)
            &.normal-enter, &.normal-leave-to
                opacity: 0
                .top
                    transform: translate3d(0, -100px, 0)
                .bottom
                    transform: translate3d(0, 100px, 0)
        .mini-player
            display: flex
            align-items: center
            position: fixed
            left: 0
            bottom: 0
            z-index: 180
            width: 100%
            height: 60px
            background: $color-highlight-background
            &.mini-enter-active, &.mini-leave-active
                transition: all 0.4s
            &.mini-enter, &.mini-leave-to
                opacity: 0
            .icon
                flex: 0 0 40px
                width: 40px
                padding: 0 10px 0 20px
                img
                    border-radius: 50%
                    &.play
                        animation: rotate 10s linear infinite
                    &.pause
                        animation-play-state: paused
            .text
                display: flex
                flex-direction: column
                justify-content: center
                flex: 1
                line-height: 20px
                overflow: hidden
                .name
                    margin-bottom: 2px
                    no-wrap()
                    font-size: $font-size-medium
                    color: $color-text
                .desc
                    no-wrap()
                    font-size: $font-size-small
                    color: $color-text-d
            .control
                flex: 0 0 30px
                width: 30px
                padding: 0 10px
                .icon-play-mini, .icon-pause-mini, .icon-playlist
                    font-size: 30px
                    color: $color-theme-d
                .icon-mini
                    font-size: 32px
                    position: absolute
                    left: 0
                    top: 0
    @keyframes rotate
        0%
            transform: rotate(0)
        100%
            transform: rotate(360deg)
</style>


